/* Navbar styles */
.navbar {
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
    margin-right: 93%;
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

/* Sidebar styles */
.nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: left 1s ease;
}

.nav-menu.active {
    left: 0;
    transition: left 0.69s ease;
}

/* Sidebar items */
.nav-menu-items {
    width: 100%;
    padding: 0;
    margin: 0;
    list-style-type: none;
}

/* Individual sidebar item */
.nav-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 8px 16px;
    list-style: none;
    height: auto;
    position: relative;
}

/* Link styling within sidebar items */
.nav-text a, .dropdown-toggle {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}
.navbar-toggle{
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

.nav-text a:hover, .dropdown-toggle:hover {
    background-color: #1a83ff;
}

/* Dropdown menu styles */
.dropdown-toggle {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.arrow {
    margin-left: auto;
    font-size: 1.2rem;
    transition: left 0.69s ease;
}

/* Dropdown menu container */
.dropdown-menu {
    opacity: 0;
    height: 0;
    overflow: hidden;
    transition: opacity 1s ease, height 0.3s ease;
    background-color: #060b26;
    position: relative;
    width: 100%;
    padding: 0;
    margin: 0;
}

/* Dropdown menu visibility */
.dropdown-menu.active {
    opacity: 1;
    height: auto;
}

/* Dropdown links */
.dropdown-menu a {
    display: block;
    padding: 10px;
    color: #fff;
    text-decoration: none;
}

.dropdown-menu a:hover {
    background-color:#293fb9;
}
